.drawer :global(.ant-drawer-body) {
    background: linear-gradient(#d06255, #ec985c);
}

:global(.ant-drawer-content-wrapper) {
    max-width: 250px;
}

.linkCol{
    min-width: 200px;
}

.menu {
    background: transparent;
}

.catalogLinkMain {
    font-size: 24px;
}

.catalogLinkDrawer {
    font-size: 24px;
    margin-bottom: 20px;
}

.menu span {
    color: white;
}

@media (min-width: 992px) {
    .menu {
        display: none;
    }
}

@media (max-width: 992px) {
    .catalogLinkMain {
        display: none;
    }
}

